<template>
	<dt-scroll-box>
	    <div slot="top">
	      	<div class="top clearfix">
				<span class="dt-bread-item">奖励</span>
				<div class="btn-box fr">
					<el-button class="dt-btn" @click="dialogShow1 = true">导出模板</el-button>
					<dt-upload-btn style="margin: 0 20px;" @upload-success="handleUploadSuccess"></dt-upload-btn>
					<el-button class="dt-btn" @click="saveData"><i class="iconfont icon-shanchu"></i>保存</el-button>
					<!-- <el-button class="dt-btn"><i class="iconfont icon-shanchu"></i>提交</el-button> -->
				</div>
			</div>
	    </div>
	    <div slot="main-content">
	      				<div class="award-page">
				<div class="search-box">
					<div class="search-item">
						<span>单位名称：</span>
						<el-select class="dt-select" v-model="searchData.orgId" placeholder="请选择">
						    <el-option
						      v-for="item in orgOptions"
						      :key="item.id"
						      :label="item.label"
						      :value="item.id">
						    </el-option>
						 </el-select>
					</div>
					<div class="search-item">
						<span>年份：</span>
						<el-select class="dt-select" v-model="searchData.year" placeholder="请选择">
						    <el-option
						      v-for="item in yearOptions"
						      :key="item"
						      :label="item + '年'"
						      :value="item">
						    </el-option>
						 </el-select>
					</div>
					<div class="search-item">
						<span>月份：</span>
						<el-select class="dt-select" v-model="searchData.month" placeholder="请选择">
						    <el-option
						      v-for="i in 12"
						      :key="i"
						      :label="i + '月'"
						      :value="i">
						    </el-option>
						 </el-select>
					</div>
					<el-button class="dt-btn" style="margin-left: 20px;" @click="handleSearch"><i class="iconfont icon-shanchu"></i>查询 </el-button>
				</div>
				<div class="opt-box">
					<span class="dt-square-btn" @click="handleAdd"><i class="el-icon-plus"></i></span>
					<span class="dt-square-btn" 
						@click="handleDelete" 
						style="margin-left: 10px;">
						<i class="el-icon-minus"></i>
					</span>
				</div>
				<el-table
				    ref="multipleTable"
				    :data="tableData"
				    tooltip-effect="dark"
				    style="width: 100%"
				    :header-cell-style="{background:'#F9FAFC',color: '#696969',textAlign: 'center'}"
				    @selection-change="handleSelectionChange">
				    <el-table-column
				      type="selection"
				      width="32">
				    </el-table-column>
				    <el-table-column
				    	show-overflow-tooltip
				      	label="项目编码">
				      	<template slot-scope="scope">
				     		<dt-table-input 
				     			:key="scope.$index" 
				     			:rowIdx="scope.$index" 
				     			:value.sync="scope.row.proNum" 
				     			:valueRequire="true"
				     			@handle-change="handleValueChange"></dt-table-input>	
			  			</template>
				    </el-table-column>
				    <el-table-column
				      prop="proName"
				      show-overflow-tooltip
				      label="项目名称">
				    	<template slot-scope="scope">
				     		<dt-table-input 
				     			:key="scope.$index" 
				     			:rowIdx="scope.$index" 
				     			:value.sync="scope.row.proName" 
				     			:valueRequire="true"
				     			@handle-change="handleValueChange"></dt-table-input>	
			  			</template>  
				    </el-table-column>
				    <el-table-column
				      prop="completionOrg"
				      label="完成单位"
				      show-overflow-tooltip>
						<template slot-scope="scope">
				     		<dt-table-input 
				     			:key="scope.$index" 
				     			:rowIdx="scope.$index" 
				     			:value.sync="scope.row.completionOrg" 
				     			:valueRequire="true"
				     			@handle-change="handleValueChange"></dt-table-input>	
			  			</template> 
				    </el-table-column>
				    <el-table-column
				      prop="completionPerson"
				      label="主要完成人"
				      show-overflow-tooltip>
				      	<template slot-scope="scope">
				     		<dt-table-input 
				     			:key="scope.$index" 
				     			:rowIdx="scope.$index" 
				     			:value.sync="scope.row.completionPerson" 
				     			:valueRequire="true"
				     			@handle-change="handleValueChange"></dt-table-input>	
			  			</template> 
				    </el-table-column>
				    <el-table-column
				      prop="audit"
				      label="奖项类别"
				      show-overflow-tooltip>
				      	<template slot-scope="scope">
				     		<dt-table-input 
				     			:key="scope.$index" 
				     			:rowIdx="scope.$index" 
				     			:value.sync="scope.row.awardCat" 
				     			:valueRequire="true"
				     			@handle-change="handleValueChange"></dt-table-input>	
			  			</template>
				    </el-table-column>
				    <el-table-column
				      prop="awardName"
				      label="奖项名称"
				      show-overflow-tooltip>
				      	<template slot-scope="scope">
				     		<dt-table-input 
				     			:key="scope.$index" 
				     			:rowIdx="scope.$index" 
				     			:value.sync="scope.row.awardName" 
				     			:valueRequire="true"
				     			@handle-change="handleValueChange"></dt-table-input>	
			  			</template> 
				    </el-table-column>
				    <el-table-column
				      prop="style"
				      label="奖项等级"
				      show-overflow-tooltip>
				      <template slot-scope="scope">
				     		<dt-table-input 
				     			:key="scope.$index" 
				     			:rowIdx="scope.$index" 
				     			:value.sync="scope.row.awardLevel" 
				     			:valueRequire="true"
				     			@handle-change="handleValueChange"></dt-table-input>	
			  			</template>
				    </el-table-column>
				</el-table>
				<!-- 保存操作 -->
				<dt-dialog key="1" :isShow="dialogShow" @left-click="handleLeftClick" @right-click="dialogShow = false" title="有未保存的数据，确定继续操作？"></dt-dialog>
				<!-- 导出模板 -->
				<dt-dialog key="2" :isShow="dialogShow1" @left-click="exportModel" @right-click="dialogShow1 = false" :title="'确定导出'+ activeorgIdName + searchData.year+'年' +  searchData.month+'月的模板？'"></dt-dialog>
			</div>
	    </div>
    </dt-scroll-box>
</template>
<script>
	/**
	 * 题录-奖励
	 * @author liujingyan 2019-02-13
	 */
	import DtUploadBtn from '@/components/DtUploadBtn'
	import DtTableInput from '@/components/DtTableInput'
	// import DtTableSelect from '@/components/DtTableSelect'
	import DtDialog from  '@/components/DtDialog'

	const TABLEKEYS = ['proNum','proName','completionOrg', 'completionPerson','awardName','awardCat','awardLevel']

	export default {
		name: 'award',
		components: {
			DtUploadBtn, DtTableInput, DtDialog
		},
	  	data() {
	    	return {
	    		pageType: 'award',
	    		tableData:[],
	    		multipleSelection: [],
	    		orgOptions:[{
	    			id: '1',
	    			label: 'XXXXXXX验收'
	    		}],
	    		yearOptions:['2016','2017','2018','2019','2020'],
	    		searchData:{
	    			orgId: '1',
	    			year: '',
	    			month: ''
	    		},
	    		tableInputShow:[],
	    		// 未保存数据
	    		unsavedData:[],
	    		// 是否保存数据  用于提醒用户未保存  后续可能需要储存到全局中
	    		savedFlag: true,
	    		dialogShow: false,
	    		dialogShow1: false
	    	}
	  	},
	  	created() {
	  		// 模拟初始化选项
	  		this.searchData.orgId = this.orgOptions[0].id
	  		let dateTime = new Date()
	  		this.searchData.year = dateTime.getFullYear() + ''
	  		this.searchData.month = dateTime.getMonth() + 1 + ''

	  		this.tableData.forEach((item)=>{
	  			let rowShow = []
	  			for(let key in item){
	  				rowShow.push(true)
	  			}
	  			this.tableInputShow.push(rowShow)
	  		})

	  		// 强制加一个索引 方便删除与查找
	  		let idx = 0
	  		this.tableData.forEach((item)=>{
	  			let rowShow = []
	  			for(let key in item){
	  				rowShow.push(true)
	  			}
	  			this.$set(item,'index',idx++)
	  			this.tableInputShow.push(rowShow)
	  		})

	  		this.initPage()
	  	},
	  	methods: {
	  		// 初始化页面 
	  		initPage () {
	  			this.$http.get('/sys-dict/codes/SRCAT+PROST')
		  		.then((res)=>{
		  			let data = res.data
		  			this.stateOptions = data.PROST
		  			this.typeOptions = data.SRCAT
		  			this.getTableData()
		  		})
		  		.catch((err)=>{

		  		})
	  		},
	  		getUrl() {
	  			return this.pageType + '\/'+ this.searchData.orgId + '\/' + this.searchData.year + '\/' + this.searchData.month
	  		},
	  		getTableData () {
	  			this.$http.get('/srpms-outline/' + this.getUrl())
	  			.then((res)=>{
	  				this.tableData = res.data || []
	  			})
	  			.catch((err)=>{

	  			})
	  		},
	  		handleAdd () {
	  			let newRow = {}
	  			let newRowShow = []
	  			TABLEKEYS.forEach((key)=>{
	  				newRow[key] = ''
	  				newRowShow.push(false)
	  			})
	  			// 添加索引
	  			newRow['index'] = this.tableData.length
	  			this.tableInputShow.push(newRowShow)
	  			this.tableData.push(newRow)
	  		},
	  		handleDelete () {
	  			if(!this.multipleSelection.length) {
	  				this.$message({
			          message: '请选择要删除的项目',
			          type: 'warning'
			        })
			        return
	  			}
	  			// 模拟删除选中
	  			this.multipleSelection.forEach((item)=>{
	  				this.tableData.splice(item.index,1)
	  			})
	  			this.multipleTable = []	
	  		},
	  		handleSelectionChange (val) {
	  			 this.multipleSelection = val
	  		},
	  		// 导出模板
	  		exportModel () {
	  			console.log('导出模板')
	  			this.dialogShow1 = false
	  		},
	  		// 保存新增/删除/修改数据
	  		saveData () {
				if(!this.unsavedData.length){
					this.$message({
			          message: '您未修改任何数据！',
			          type: 'warning'
			        })
					return
				}

				let newArr = []
				this.unsavedData.forEach((idx)=>{
					newArr.push(this.tableData[idx])
				})

				let submitData = {
					rows: newArr
				}

				console.log(JSON.stringify(submitData))
				this.$http.post('/srpms-outline/'+ this.getUrl(),JSON.stringify(submitData))
				.then((res)=>{
					if(res.code === '000000'){
						this.$message({
				          message: '保存成功！',
				          type: 'success'
				        })
				        this.getTableData()
					}
				})
				.catch((err)=>{

				})
	  		},
	  		// 上传成功
	  		handleUploadSuccess () {
	  			this.$message({
		          message: '导入成功！',
		          type: 'success'
		        })
	  		},
	  		formatterType (val) {
	  			let item = this.allTypes.filter((o)=> {
	  				return String(o.value) === String(val) 
	  			})

	  			return item && item.length ? item[0].label:''
	  		},
	  		handleAdd () {
	  			let newRow = {}
	  			let newRowShow = []
	  			TABLEKEYS.forEach((key)=>{
	  				newRow[key] = ''
	  				newRowShow.push(false)
	  			})
	  			// 添加索引
	  			newRow['index'] = this.tableData.length
	  			this.tableInputShow.push(newRowShow)
	  			this.tableData.push(newRow)
	  		},
	  		handleDelete () {
	  			if(!this.multipleSelection.length) {
	  				this.$message({
			          message: '请选择要删除的项目',
			          type: 'warning'
			        })
			        return
	  			}
	  			// 模拟删除选中
	  			this.multipleSelection.forEach((item)=>{
	  				this.tableData.splice(item.index,1)
	  			})
	  			this.multipleTable = []	
	  		},
	  		handleSearch () {
	  			if(!this.validateSaveFlag()) return
	  			// 发起请求
	  			this.getTableData()
	  		},
	  		// 数据改变之后保存索引值到数组中
	  		handleValueChange (rowIdx) {
	  			this.savedFlag = false
	  			let isInArray = this.unsavedData.findIndex((o)=>{
	  				return o === rowIdx
	  			})

	  			if (isInArray === -1) {
	  				this.unsavedData.push(rowIdx)
	  			}
	  		},
	  		validateSaveFlag () {
	  			if (!this.savedFlag) {
	  				this.dialogShow = true
	  				return false
	  			} else {
	  				return true
	  			}
	  		},
	  		handleLeftClick () {
	  			this.savedFlag = true
	  			this.dialogShow = false
	  			this.handleSearch()
	  		}
	  	},
	  	computed: {
	  		activeorgIdName () {
	  			let idx = this.orgOptions.findIndex((o)=>{
	  				return o.id === this.searchData.orgId
	  			})

	  			return idx !== -1 ? this.orgOptions[idx].label:''
	  		}
	  	}
	}
</script>
<style lang="less">
	.award-page{
		padding-bottom: 60px;
		.top{
			margin-bottom: 30px;
		}
		.search-item{
			display: inline-block;
			>span{
				padding-right: 6px;
				font-size: 16px;
				color: #696969;
			}
		}
		.search-item+.search-item{
			margin-left: 20px;
		}
		.opt-box{
			margin: 40px 0 20px 37px;
		}
	}
</style>


